@charset "UTF-8";
.contact {
  font-family: "游ゴシック体", "Yu Gothic", YuGothic, sans-serif;
  box-sizing: border-box;
}

.contact li {
  list-style: none;
}

.contact .contact__top {
  height: 300px;
  position: relative;
}

.contact .contact__top .contact__top__text {
  font-size: 3.8rem;
  font-family: noto-sans-cjk-jp,sans-serif;
  font-weight: 400;
  color: #111;
  margin: 0;
  position: absolute;
  top: calc(50% - 1.9rem);
}

.contact .contact__navi {
  margin: 25px 0;
}

.contact .contact__navi .contact__navi__list {
  display: flex;
  font-size: 1.3rem;
}

.contact .contact__navi .contact__navi__list a {
  color: #EB5E00;
}

.contact .contact__content {
  padding: 20px 0 30px;
}

.contact .contact__content .contact__content__title {
  font-size: 3.2rem;
  font-family: noto-sans-cjk-jp,sans-serif;
  font-weight: 400;
  position: relative;
  text-align: center;
  margin-bottom: 25px;
}

.contact .contact__content .contact__content__title:after {
  position: absolute;
  bottom: calc(50% - 7px);
  left: 0;
  width: 100%;
  height: 10px;
  content: '';
  background-image: -webkit-repeating-linear-gradient(135deg, #71C0CB, #71C0CB 1px, transparent 2px, transparent 5px);
  background-image: repeating-linear-gradient(-45deg, #71C0CB, #71C0CB 1px, transparent 2px, transparent 5px);
  background-size: 7px 7px;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
}

.contact .contact__content .contact__content__title span {
  padding: 0 25px;
  background: #fff;
  position: relative;
  z-index: 10;
}

.contact .contact__content .contact__content__sub-title {
  font-size: 2.4rem;
  color: #fff;
  background: #eb5e00;
  border-left: solid 6px #ff8748;
  padding: 13px 20px;
  margin-bottom: 25px;
  font-family: noto-sans-cjk-jp,sans-serif;
  font-weight: 400;
}

.contact .contact__content .contact__form {
  color: #333;
  line-height: 22.4px;
}

.contact .contact__content .contact__form th, .contact .contact__content .contact__form td {
  padding: 12px;
  font-size: 1.4rem;
  text-align: left;
}

.contact .contact__content .contact__form th {
  background: #ff8748;
  color: #fff;
  font-weight: bold;
  width: 20%;
  border: 1px solid #ff8748;
  border-right-color: #fff;
  border-bottom-color: #fff;
}

.contact .contact__content .contact__form th .mandatory {
  color: #fff;
  position: relative;
}

.contact .contact__content .contact__form th .mandatory:after {
  content: "必須";
  font-size: 1.1rem;
  line-height: 1;
  color: #fff;
  padding: 2px;
  background: #DE7067;
  position: absolute;
  right: -28px;
  bottom: -1px;
  border-radius: 2px;
}

.contact .contact__content .contact__form td {
  width: 80%;
  border: solid 1px #cdcdcd;
  border-radius: 2px;
}

.contact .contact__content .contact__form td .contact__form__caution {
  color: #009900;
}

.contact .contact__content .contact__form td input[type="text"] {
  padding: 5px;
  border: solid 1px #767676;
  width: 100%;
  color: #333;
  line-height: 19.6px;
}

.contact .contact__content .contact__form td input[type="text"]:hover {
  border: solid 1px #000;
}

.contact .contact__content .contact__form td .mwform-radio-field-text,
.contact .contact__content .contact__form td .mwform-checkbox-field-text {
  padding-right: 0;
  line-height: 22.4px;
}

.contact .contact__content .contact__form td label {
  border: dotted 1px #FFCE3C;
  border-radius: 3px;
  padding: 8px 6px 6px;
  margin-right: 10px;
  line-height: 22.4px;
}

.contact .contact__content .contact__form td label:hover {
  background: #FFCE3C;
}

.contact .contact__content .contact__form td input[type="radio"]:checked + label {
  background: #FFCE3C;
}

.contact .contact__content .contact__form td input[type="radio"] + span::before {
  width: 13px;
  height: 13px;
  border: solid 1px #333;
  border-radius: 50%;
}

.contact .contact__content .contact__form td input[type="radio"] + span:hover::after {
  background: #fff;
}

.contact .contact__content .contact__form td input[type="radio"]:checked + span::before {
  border: solid 1px #2C77F6;
}

.contact .contact__content .contact__form td input[type="radio"]:checked + span::after {
  background: #2C77F6;
  width: 9px;
  height: 9px;
  left: -4.5px;
  margin-top: -5px;
  z-index: 10;
}

.contact .contact__content .contact__form td input[type="radio"]:checked + .mwform-radio-field-text {
  color: #333;
}

.contact .contact__content .contact__form td input[type="checkbox"] + span::before {
  width: 13px;
  height: 13px;
  border: solid 1px #333;
  border-radius: 3px;
}

.contact .contact__content .contact__form td input[type="checkbox"] + span:hover::after {
  background: rgba(255, 255, 255, 0);
}

.contact .contact__content .contact__form td input[type="checkbox"]:checked + span::before {
  border: solid 1px #2C77F6;
  background: #2C77F6;
}

.contact .contact__content .contact__form td input[type="checkbox"]:checked + span::after {
  content: "";
  position: absolute;
  width: 8px;
  height: 4px;
  margin-top: -5px;
  top: 50%;
  left: -4.5px;
  transform: rotate(-45deg);
  border-bottom: 2px solid #fff;
  border-left: 2px solid #fff;
  background: #2C77F6;
}

.contact .contact__content .contact__form td input[type="checkbox"]:checked + .mwform-checkbox-field-text {
  color: #333;
}

.contact .contact__content .contact__form td .error {
  font-size: 1.3rem;
  color: #B70000;
  display: block;
}

.contact .contact__content .contact__form .confirm-btn {
  width: 290px;
  height: 60px;
  font-size: 1.6rem;
  color: #fff;
  background: #EB5E00;
  font-family: noto-sans-cjk-jp,sans-serif;
  font-weight: 400;
  margin: 20px 390px;
  text-align: center;
  align-items: center;
  padding: 10px 25px;
  border: solid 1px #EB5E00;
}
